/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState } from "react";
import "./App.css";
import logo from "../assets/img/LOGO-PHILA.png";
import {
  Button,
  Col,
  Checkbox,
  Form,
  Input,
  Select,
  Row,
  InputNumber,
} from "antd";
import { Snackbar } from "@mui/material";
import MuiAlert from "@mui/material/Alert";
import axios from "axios";
import moment from "moment";
import "moment/locale/fr";
const { Option } = Select;

const AlertInfos = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const formItemLayout = {
  labelCol: {
    xs: {
      span: 24,
    },
    sm: {
      span: 8,
    },
  },
  wrapperCol: {
    xs: {
      span: 24,
    },
    sm: {
      span: 16,
    },
  },
};

const tailFormItemLayout = {
  wrapperCol: {
    xs: {
      span: 24,
      offset: 0,
    },
    sm: {
      span: 16,
      offset: 8,
    },
  },
};

const Members = () => {
  const [form] = Form.useForm();
  const [open, setOpen] = useState(false);
  const [openError, setOpenError] = useState(false);
  const [error, setError] = useState("");

  const [firstname, setFirstname] = useState("");
  const [lastname, setLastname] = useState("");
  const [phone, setPhone] = useState("");
  const [family, setFamily] = useState("");
  const [adult, setAdult] = useState("");
  const [children, setChildren] = useState("");
  const [car, setCar] = useState("");
  const [service, setService] = useState("");

  const onFinish = (e) => {
    if (firstname && lastname && phone) {
      axios
        .post(`${process.env.REACT_APP_API_URL}/members`, {
          firstname,
          lastname,
          phone,
          family,
          adult,
          children,
          car,
          service,
          date: moment().format("L"),
        })
        .then(() => {
          setOpen(true);
          form.resetFields();
        })
        .catch((err) => {
          setError(err.response.data.message);
          setOpenError(true);
        });
    }
  };

  const handleCloseAlert = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpenError(false);
    setOpen(false);
  };

  return (
    <div className="text-center form-container">
      <img src={logo} alt="logo" className="App-logo container mb-3 mt-5" />
      <Form
        {...formItemLayout}
        form={form}
        name="register"
        onFinish={onFinish}
        initialValues={{
          prefix: "33",
        }}
        style={{
          maxWidth: 600,
        }}
        scrollToFirstError
        className="container pt-3 mt-3 form col-11"
      >
        <Form.Item
          name="lastname"
          label="Nom"
          rules={[
            {
              required: true,
              message: "Veuillez entrer votre nom !",
              whitespace: true,
            },
          ]}
          className=""
        >
          <Input
            placeholder="Votre nom"
            value={lastname}
            onChange={(e) => setLastname(e.target.value)}
            // className="form-input"
          />
        </Form.Item>

        <Form.Item
          name="firstname"
          label="Prnom"
          rules={[
            {
              required: true,
              message: "Veuillez entrer votre prnom!",
              whitespace: true,
            },
          ]}
        >
          <Input
            placeholder="Votre prnom"
            value={firstname}
            onChange={(e) => setFirstname(e.target.value)}
            // className="form-input"
          />
        </Form.Item>

        <Form.Item
          name="phone"
          label="Tlphone"
          rules={[
            {
              required: true,
              message: "Veuillez entrer votre tlphone!",
            },
          ]}
        >
          <Input
            style={{
              width: "100%",
            }}
            onChange={(e) => setPhone(e.target.value)}
            placeholder="0123456789"
            // className="form-input"
            value={phone}
          />
        </Form.Item>

        <Form.Item
          name="famille"
          label="Venez-vous au culte en famille ?"
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Select
            value={family}
            placeholder="Votre famille"
            onChange={(e) => setFamily(e)}
            // className="form-input"
            // style={{ backgroundColor: "transparent" }}
          >
            <Option value="Oui">Oui</Option>
            <Option value="Non">Non</Option>
          </Select>
        </Form.Item>

        {family === "Oui" && (
          <Form.Item
            name="nombre"
            label="Nombres"
            rules={[
              {
                required: true,
              },
            ]}
          >
            <Row gutter={8}>
              <Col span={12}>
                <Form.Item
                  name="adulte"
                  rules={[
                    {
                      required: true,
                    },
                  ]}
                >
                  <Input
                    placeholder="Adultes"
                    // className="form-input"
                    onChange={(e) => setAdult(e.target.value)}
                  />
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item
                  name="enfants"
                  rules={[
                    {
                      required: true,
                    },
                  ]}
                >
                  <Input
                    placeholder="Enfants"
                    // className="form-input"
                    onChange={(e) => setChildren(e.target.value)}
                  />
                </Form.Item>
              </Col>
            </Row>
          </Form.Item>
        )}

        <Form.Item
          name="car"
          label="Avez-vous un vhicule ?"
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Select
            value={car}
            placeholder="Avez-vous un vhicule ?"
            onChange={(e) => setCar(e)}
            popupClassName="form-input"
          >
            <Option value="Oui">Oui</Option>
            <Option value="Non">Non</Option>
          </Select>
        </Form.Item>

        <Form.Item
          name="service"
          label="A quel culte participez-vous habituellement ?"
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Select
            value={service}
            placeholder="Votre culte"
            onChange={(e) => setService(e)}
            popupClassName="form-input"
          >
            <Option value="1er culte">1er culte</Option>
            <Option value="2eme culte">2eme culte</Option>
          </Select>
        </Form.Item>

        <Form.Item
          name="agreement"
          valuePropName="checked"
          rules={[
            {
              validator: (_, value) =>
                value
                  ? Promise.resolve()
                  : Promise.reject(new Error("Should accept agreement")),
            },
          ]}
          {...tailFormItemLayout}
        >
          <Checkbox>
            J'accepte les{" "}
            <a href="#" style={{ color: "white", textDecoration: "none" }}>
              conditions d'utilisation
            </a>
          </Checkbox>
        </Form.Item>

        <Form.Item {...tailFormItemLayout}>
          <Button type="primary" htmlType="submit" className="btn-submit">
            Envoyer
          </Button>
        </Form.Item>
        {/* </Form.Item> */}

        <Snackbar
          open={open}
          autoHideDuration={6000}
          onClose={handleCloseAlert}
        >
          <AlertInfos
            onClose={handleCloseAlert}
            severity="success"
            sx={{ width: "100%" }}
          >
            Envoy avec succes !
          </AlertInfos>
        </Snackbar>

        <Snackbar
          open={openError}
          autoHideDuration={6000}
          onClose={handleCloseAlert}
        >
          <AlertInfos
            onClose={handleCloseAlert}
            severity="error"
            sx={{ width: "100%" }}
          >
            {error && error}
          </AlertInfos>
        </Snackbar>
      </Form>
    </div>
  );
};

export default Members;
