import React, { useEffect, useState } from "react";
import { Box, SpeedDial, SpeedDialIcon, SpeedDialAction } from "@mui/material";
import FileCopyIcon from "@mui/icons-material/FileCopyOutlined";
import PictureAsPdfIcon from "@mui/icons-material/PictureAsPdf";
import { CSVDownload } from "react-csv";
import axios from "axios";

const actions = [
  { icon: <FileCopyIcon />, name: "Excel" },
  { icon: <PictureAsPdfIcon />, name: "Pdf" },
];

const Speedial = () => {
  const [members, setMembers] = useState([]);
  const [csv, setCsv] = useState(false);
  const [pdf, setPdf] = useState(false);

  const handleExportCsv = () => {
    setCsv(true);

    setTimeout(() => {
      window.location.reload();
    }, 10000);
  };

  const getMembers = async () => {
    await axios
      .get(`${process.env.REACT_APP_API_URL}/members`)
      .then((value) => {
        setMembers(value.data);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  useEffect(() => {
    getMembers();
  }, []);

  const headers = [
    { label: "Nom", key: "lastname" },
    { label: "Prnom", key: "firstname" },
    { label: "Tlphone", key: "phone" },
    { label: "Famille", key: "family" },
    { label: "Adultes", key: "adult" },
    { label: "Enfants", key: "children" },
    { label: "Vhicule", key: "car" },
    { label: "Culte", key: "service" },
    { label: "Date", key: "date" },
  ];

  const dataMembers = (data) => {
    return data.map((item) => ({
      ...item,
      family: item.family === "Oui" ? "En famille" : "Pas famille",
      car: item.car === "Oui" ? "Vhicul" : "Non vhicul",
    }));
  };

  const dataFormat = dataMembers(members);

  return (
    <div className="pb-3">
      <Box sx={{ height: "", transform: "translateZ(0px)", flexGrow: 1 }}>
        <SpeedDial
          ariaLabel="SpeedDial basic example"
          sx={{ position: "absolute", bottom: 16, right: 16 }}
          icon={<SpeedDialIcon />}
        >
          {actions.map((action) => (
            <SpeedDialAction
              key={action.name}
              icon={action.icon}
              tooltipTitle={action.name}
              onClick={action.name === "Excel" ? handleExportCsv : null}
            />
          ))}
        </SpeedDial>
      </Box>

      {csv && (
        <CSVDownload
          data={dataFormat}
          headers={headers}
          separator={";"}
          filename={"Lites_fideles.csv"}
        />
      )}
    </div>
  );
};

export default Speedial;
